body {
  font-size: 20px;
}
#menu {
  height: 15vh;
}

#body {
  text-align: center;
  height: auto;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-bottom-style: none;
  box-shadow: 0 1px 0 0 white;
}

#distance {
  background-color: white;
  border: 1px solid rgba(34, 36, 38, 0.15);
  width: 60%;
  height: 40px;
  margin-top: 20px;
  margin-bottom: 20px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
}

#selecting {
  width: 60%;
  display: flex;
  justify-content: space-between;
}

#from {
  width: 45%;
}

#to {
  width: 45%;
}

#button {
  width: 60%;
}

@media (max-width: 800px) {
  #selecting {
    flex-direction: column;

    align-items: center;
  }

  #from {
    width: 100%;
  }

  #to {
    width: 100%;
  }
}
.button {
  background-color: #ec9d23;
  border-radius: 20px;
  padding: 5px 15px 5px 15px;
  cursor: pointer;
  width: 200px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}
